@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
body {
  margin: 0;
  font-family: 'Russo One',-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #303030;
  color: #FFFFFF;
  background-image: url(/static/media/fond2.8db7b0c4.jpg);
  background-size: cover;
  background-position: bottom bottom;
  background-repeat: no-repeat;
  color: #fff;
  font-weight: 700;
  font-size:small;
}

.milieu{
  width:352px;
  margin:0 auto;
}

.center{
  text-align: center;
  padding-bottom: 10px;
}

a {
  color: #fff;
}

.lien{
  color: rgb(255, 247, 55);
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#root{
  background-color: #000;
  display: grid;
  text-align: center;
  place-content: center;
}
.App {
  text-align: center;
}

App, p {
  text-align: center;
}

p {
  margin: auto;
  width: 332px;
  padding: 10px;
}

q {
  color: #ffae00;
}

Button{
  background-color: #ff4040;
  font-size: 15px;
  font-weight: bold;
  font-family: Russo One;
}
